const BN = require('bn.js');
const dict = "0123456789abcdefghjkmnpqrtuvwxyz";
const dictLength = 32;

var MOD = new BN(4294967296);

export function encode(n) {
    var x = new BN(n).mod(MOD);
    x = x.shrn(16).xor(x).mul(new BN(73244475)).mod(MOD);
    x = x.shrn(16).xor(x).mul(new BN(73244475)).mod(MOD);
    x = x.shrn(16).xor(x);
    x = x.toNumber();

    let ret = "";
    while (x > 0) {
        let round = x / dictLength;
        let remain = x % dictLength;
        ret = dict.charAt(remain) + ret;
        x = Math.floor(round);
    }
    return ret;
}

export function decode(code) {
    let n = 0;
    let len = code.length;
    for (var i = 0; i < len; i++) {
        n += dict.indexOf(code[i]) * Math.pow(dictLength, len - 1 - i);
    }
    var x = new BN(n).mod(MOD)
    x = x.shrn(16).xor(x).mul(new BN(295559667)).mod(MOD);
    x = x.shrn(16).xor(x).mul(new BN(295559667)).mod(MOD);
    x = x.shrn(16).xor(x);
    return x.toNumber();
}

